:root {
  --main-color: #8c9eff;
  --white: #ffffff;
  --black: #000000;
  --DotGothic16: "DotGothic16", sans-serif;
  --Poppins: "Poppins", sans-serif;
  --gray: #808080;
  --off-white: #dedede;
  --hover-color: #bbdefb;
}

::-webkit-input-placeholder {
  color: #000000 !important;
  color: var(--black) !important;
}

:-ms-input-placeholder {
  color: #000000 !important;
  color: var(--black) !important;
}

::placeholder {
  color: #000000 !important;
  color: var(--black) !important;
}

.mainHeight {
  min-height: 100vh;
  background: url(/static/media/background.c877ccd3.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}

.heading {
  min-width: 100%;
  min-height: 25vh;
}

.mainContent {
  min-width: 100%;
  min-height: 65vh;
}

.iconMain {
  color: #8c9eff;
  color: var(--main-color);
}

.headingMain {
  font-family: "DotGothic16", sans-serif;
  font-family: var(--DotGothic16);
}

.wrapper {
  min-height: 100%;
  align-items: center;
}

.inputBackground {
  background-color: rgba(255, 255, 255, 0.5);
}

.borderLeft {
  border-left: 1px solid #ffffff;
  border-left: 1px solid var(--white);
}

.borderRight {
  border-right: 1px solid #ffffff;
  border-right: 1px solid var(--white);
}

.formHeight {
  min-height: 100% !important;
  letter-spacing: 0.1rem;
}

.sectionHeading {
  font-family: "DotGothic16", sans-serif;
  font-family: var(--DotGothic16);
}

.chatPage {
  min-width: 100%;
  min-height: 100vh;
  background: url(/static/media/backgroundChat.530aadcd.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.chatWrapper {
  background: #ffffff;
  background: var(--white);
  width: 70%;
  min-height: 80vh;
  max-height: 80vh;
  display: flex;
  overflow: hidden;
}

.chathistory {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  flex: 1 1;
}

.chats {
  flex: 3 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.title {
  width: 100%;
  min-height: 15%;
  max-height: 15%;
  padding: 0 1rem 0 2rem;
}

.titleText {
  font-weight: 500;
  font-size: 1.5rem;
  color: #8c9eff;
  color: var(--main-color);
  font-family: "Poppins", sans-serif;
  font-family: var(--Poppins);
}

.chatHistoryWrapper {
  flex: 1 1;
  width: 100%;
  padding: 0 0.5rem 1rem 0.5rem;
}

.chatHistoryMessageWrapper {
  padding: 1rem 0.5rem 1rem 1rem;
  border-bottom: 1px solid rgba(128, 128, 128, 0.5);
  transition: ease-in-out 0.3s;
  display: flex;
  cursor: pointer;
}

.userIcon {
  flex: 1 1;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-right: 1rem;
}

.userPic {
  width: 100%;
}

.userData {
  flex: 3 1;
}

.chatHistoryMessageWrapper:hover {
  background-color: rgba(140, 158, 255, 0.4);
}

.chatName {
  font-size: 1.5rem;
  font-weight: 300;
}

.chatLastMessage {
  font-size: 0.8rem;
  padding-left: 0.5rem;
  color: #8c9eff;
  color: var(--main-color);
}

.sectionTabs {
  padding: 0.8rem 0.5rem 0.8rem 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #ffffff;
  color: var(--white);
  font-weight: bold;
  font-size: 0.8rem;
  font-family: "Poppins", sans-serif;
  font-family: var(--Poppins);
  cursor: pointer;
  border: 1px solid #ffffff;
  border: 1px solid var(--white);
  border-top: none;
  background-color: #8c9eff;
  background-color: var(--main-color);
  margin-bottom: -5px;
  transition: ease-in-out 0.2s;
}

.sectionTabs:nth-child(1) {
  border-left: none;
}

.sectionTabs:nth-child(3) {
  border-right: none;
}

.active {
  border-top: 5px solid #8c9eff;
  border-top: 5px solid var(--main-color);
  background-color: #ffffff;
  background-color: var(--white);
  color: #8c9eff;
  color: var(--main-color);
}

.contactTitle {
  padding-top: 1.2rem;
}

.searchBar {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem 0.5rem 1rem 0.5rem;
  background-color: #dedede;
  background-color: var(--off-white);
}

.searchBarInput {
  padding: 0.5rem 1rem 0.5rem 1rem;
  border-radius: 25px;
  outline: none;
  border: 2px solid #8c9eff;
  border: 2px solid var(--main-color);
}

.searchBarInput::-webkit-input-placeholder {
  color: #8c9eff !important;
  color: var(--main-color) !important;
}

.searchBarInput:-ms-input-placeholder {
  color: #8c9eff !important;
  color: var(--main-color) !important;
}

.searchBarInput::placeholder {
  color: #8c9eff !important;
  color: var(--main-color) !important;
}

.addIcon {
  display: flex;
  align-items: center;
  justify-content: start;
  flex: 1 1;
  font-size: 1.2rem;
  color: #4caf50;
}

.chatTopBar {
  flex: 1 1;
  width: 100%;
  max-height: 10vh;
}

.topBarSection {
  min-height: 10vh;
  max-height: 10vh;
  overflow: hidden;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.topbarImageWrapper {
  flex: 1 1;
  padding: 0.5rem 0.5rem 0.5rem 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.topBarImage {
  object-fit: contain;
  width: 70%;
}

.tapBarName {
  flex: 8 1;
  font-size: 1.5rem;
  display: flex;
  justify-content: flex-start;
  min-height: 10vh;
}

.chatMessages {
  flex: 4 1;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.messagesSection {
  flex: 1 1;
  width: 100%;
  background: url(/static/media/messageBackground.0d86f61f.jpg);
  background-size: cover; /* or contain depending on what you want */
  background-position: center center;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column-reverse;
  overflow-y: scroll;
  max-height: 60vh;
}

.messageWrapper {
  padding: 1rem 1.5rem 1rem 1.5rem;
  margin-top: 1rem;
  display: flex;
  width: 100%;
  max-width: 100%;
}

.message {
  margin-bottom: 0.5rem;
  padding: 0.8rem 1rem 0.8rem 1rem;
  border-radius: 25px;
  word-wrap: break-word;
  max-width: 300px;
}

.sender {
  justify-content: flex-end;
}

.sender .message {
  background-color: #009688;
}

.reciver {
  justify-content: flex-start;
}

.reciver .message {
  background-color: #bbdefb;
}

.messageInputSection {
  max-height: 10vh;
  width: 100%;
  background-color: #eeeeee;
  flex: 1 1;
  display: flex;
